import React, { Fragment } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import style from './style'
import Grid from '@material-ui/core/Grid'

const useStyles = makeStyles(style)

export default ({ activity: originalActivity }) => {
  const [activity, setActivity] = React.useState(originalActivity)
  const [choice, setChoice] = React.useState({})

  const onDragStart = (ev, id, index) => {
    id = ' ' + id + ' '
    ev.dataTransfer.setData('id', JSON.stringify({ id, index }))
  }

  const onDragOver = ev => {
    ev.preventDefault()
  }

  const onDrop = (ev, dropIndex) => {
    let id = JSON.parse(ev.dataTransfer.getData('id'))
    choice[dropIndex] = id
    setChoice({ ...choice })
  }

  const createDropArea = (s, index) => (
    <Fragment key={index}>
      <span className={classes.wip} onDragOver={e => onDragOver(e)} onDrop={e => onDrop(e, index)}>
        <span
          key={s}
          onDragStart={e => onDragStart(e, s, index)}
          draggable
          className={classes.droppable}
        >
          {(choice[index] && choice[index].id) || s.replace(/./g, '_')}
        </span>
      </span>
    </Fragment>
  )

  const createDragArea = (s, index) => {
    if (!Object.values(choice).some(k => k.index == index)) {
      return (
        <span
          key={index}
          onDragStart={e => onDragStart(e, s, index)}
          draggable
          className={classes.draggable}
        >
          {s}
        </span>
      )
    } else {
      return null
    }
  }

  const replaceNewLineWithBreak = s =>
    s.split('\n').map((item, key) => (
      <Fragment key={key}>
        {key > 0 && <br />}
        {item}
      </Fragment>
    ))

  const classes = useStyles()
  const strings = activity.extra
    .split('*')
    .map((s, index) =>
      index % 2 === 0 ? replaceNewLineWithBreak(s, index) : createDropArea(s, index)
    )
  const strings1 = activity.extra
    .split('*')
    .map((s, index) => (index % 2 === 1 ? createDragArea(s, index) : <br key={index} />))

  return (
    <Grid container spacing={3} className={classes.root}>
      <Grid item xs={6}>
        {strings1.map(str => str)}
      </Grid>
      <Grid item xs={6}>
        {strings.map(str => str)}
      </Grid>
      <Grid item xs={12}>
        <Button variant='contained' color='primary'>
          Check
        </Button>
      </Grid>
    </Grid>
  )
}
